"use client";

import { motion } from "framer-motion";
import Link from "next/link";
import Image from "next/image";
import { BarChart2 } from "lucide-react";

interface AuthLayoutProps {
    children: React.ReactNode;
    title: string;
    subtitle: string;
}

export default function AuthLayout({ children, title, subtitle }: AuthLayoutProps) {
    return (
        <div className="min-h-screen bg-[#0B1220] flex flex-col items-center justify-center px-4 py-10">
            {/* HEADER */}
            <div className="text-center space-y-2">
                <Link href="/" className="inline-flex items-center gap-2 group mb-4">
                    <div className="bg-blue-600 p-2 rounded-xl group-hover:scale-105 transition-transform shadow-lg shadow-blue-500/20">
                        <BarChart2 className="w-6 h-6 text-white" />
                    </div>
                    <span className="text-xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-blue-600 to-cyan-500">
                        NSE Analyzer
                    </span>
                </Link>
                <motion.div
                    initial={{ opacity: 0, y: -10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5 }}
                >
                    <h1 className="text-3xl font-semibold text-white">{title}</h1>
                    <p className="text-gray-400 mt-2">{subtitle}</p>
                </motion.div>
            </div>

            {/* IMAGE (Desktop Only) */}
            <motion.div
                initial={{ opacity: 0, scale: 0.95 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.5, delay: 0.2 }}
                className="hidden lg:block mt-8 relative w-[500px] h-[280px] rounded-xl overflow-hidden shadow-2xl shadow-blue-900/20 border border-white/10"
            >
                <Image
                    src="/images/vr-hero-vertical.png"
                    alt="Future of Trading"
                    fill
                    className="object-cover"
                    priority
                />
                <div className="absolute inset-0 bg-gradient-to-t from-[#0B1220]/80 via-transparent to-transparent" />
            </motion.div>

            {/* FORM */}
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: 0.3 }}
                className="mt-8 w-full max-w-md bg-[#111827] p-8 rounded-2xl shadow-xl border border-gray-800"
            >
                {children}
            </motion.div>
        </div>
    );
}
